.contact-us-card-container {
  display: -ms-flexbox;
  display: flex;
}

.contact-us-card-internal {
  width: 470px;
  height: 421px;
  background-image: url("../../assets/Remanso-08.png");
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.contact-us-card-internal form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;  
  position: absolute;
  width: 23%;
  margin-top: 28px;
}

.contact-us-card-internal input {
  width: 100%;
  margin: 7px; 
  border-radius: 5px;
  border: none;
  padding: 5px;
}

.contact-us-card-internal textarea {
  width: 100%;  
  margin: 7px;   
  border-radius: 5px;
  border: none;  
  padding: 5px;  
}

.contact-us-card-internal label {
  color: white; 
  font-size: 13px;
  margin: 10px 0px;
}

.contact-us-card-internal span {
  font-weight: bold;
}

.contact-us-card-internal button {
  width: 100px;
  height: 30px;
  background-color: #46200d;
  color: white;
  font-size: 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}




